.segmentation input,
.segmentation select {
  display: inline-block;
}
.segmentation .form-control {
  width: auto;
}
.segmentation select.form-control,
.segmentation input.form-control {
  padding: 3px;
  height: 28px;
}
.segmentation div.level {
  float: left;
  width: 180px;
}
.segmentation .select2-selection {
  border-radius: 0px;
  padding: 3px;
}
.segmentation select#event {
  width: 443px;
}
.segmentation .upper-bar {
  margin-top: 30px;
}
.segmentation .upper-bar .group-by {
  width: 230px;
  float: left;
  margin-right: 10px;
}
.segmentation .upper-bar .group-by select {
  width: 150px;
}
.segmentation .upper-bar .interval {
  width: 156px;
  float: left;
  clear: both;
  margin-right: 10px;
}
.segmentation .upper-bar .target-property select {
  width: 150px;
}
.segmentation .upper-bar .aggregation {
  float: left;
  width: 184px;
  margin-right: 10px;
}
.segmentation .upper-bar .daterange {
  height: 28px;
  padding: 3px 12px;
  width: 385px !important;
  margin-right: 5px;
}
.segmentation .upper-bar button {
  padding: 3px 12px;
}
.segmentation .graph {
  clear: both;
  width: 100%;
  padding-right: 50px;
  margin-top: 20px;
}
.segmentation .graph .graph-area {
  width: 100%;
  height: 400px;
}
.segmentation .graph .graph-area.graph-one-value-container {
  background-color: #00A65A;
  vertical-align: middle;
  text-align: center;
  line-height: 400px;
}
.segmentation .graph .graph-one-value {
  color: white;
  text-align: center;
  vertical-align: middle;
  font-size: 60px;
  font-weight: bold;
}
.segmentation #export-image {
  margin-right: 50px;
  margin-top: 20px;
  display: none;
  width: 200px;
}
