.log input,
.log select {
  display: inline-block;
  width: auto;
}
.log .upper-control .add-filter {
  float: left;
  margin-left: 10px;
  margin-right: 10px;
}
.log .upper-control input[name="message-search"] {
  width: 400px;
  float: left;
}
.log .page-numbers {
  text-align: center;
}
.log-records {
  font-family: "Consolas", "Monaco", "Courier New", Courier, monospace;
  margin-top: 20px;
}
.log-records pre {
  font-family: "Consolas", "Monaco", "Courier New", Courier, monospace;
}
.log-records .box {
  border-top: none;
  margin: 0px;
  padding-left: 5px;
}
.log-records .box:first-of-type {
  padding-top: 5px;
}
.log-records .box:last-of-type {
  padding-bottom: 5px;
}
.log-records .box .box-header {
  padding: 1px;
}
.log-records .box .box-header .text {
  width: 100%;
  padding-right: 100px;
  overflow: hidden;
  float: left;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.log-records .box .box-header .more {
  width: 100px;
  top: 0px;
  right: 0px;
}
.log-records .box .box-header .more a {
  padding: 1px;
  padding-right: 10px;
  float: right;
}
.log-records .box .box-body {
  background-color: #F7F7F7;
}
.log-graph {
  width: 100%;
  height: 100px;
  padding-right: 50px;
  clear: both;
}
.log-graph .log-graph-area {
  width: 100%;
  height: 100px;
}
@media (max-width: 730px) {
  .log .filters input,
  .log .filters select {
    width: 100% !important;
  }
}
@media (min-width: 730px) and (max-width: 955px) {
  .log .filters input,
  .log .filters select {
    width: 115px !important;
  }
}
@media (max-width: 520px) {
  .log .upper-control input[name="message-search"] {
    width: 100%;
  }
  .log .upper-control .daterange {
    width: 100%;
  }
  .log .upper-control .daterange i {
    display: none;
  }
}
