.grey-info {
  font-size: 12px;
  color: grey;
}
div.errors ul {
  margin-bottom: 0px;
}
.message-div {
  padding: 15px;
  margin-top: 5px;
  margin-bottom: 5px;
}
label {
  font-weight: normal;
}
.maincolor-box {
  color: white;
  background-color: #00A65A;
}
.maincolor-box:hover,
.maincolor-box:active,
.maincolor-box:focus {
  color: white;
  background-color: #009049;
}
.inline-block {
  display: inline-block;
}
ul.pagination li.active a {
  background-color: #00A65A;
  border-color: #009049;
}
ul.pagination li.active a:hover,
ul.pagination li.active a:active,
ul.pagination li.active a:focus {
  background-color: #00A65A;
  border-color: #009049;
}
.loader {
  display: none;
  width: 100px;
  height: 100px;
  position: fixed;
  top: 50%;
  margin-top: -50px;
  left: 50%;
  margin-left: 65px;
  background-image: url("../images/loader-ring.gif");
  background-size: 100%;
}
.daterange {
  width: 400px;
  overflow: hidden;
  float: left;
}
.daterange label {
  margin-left: 5px;
}
.down-control {
  clear: both;
  display: none;
}
.down-control #add-filter-row {
  margin-top: 5px;
}
.down-control #example-filter-row {
  display: none;
}
.down-control .remove-filter-row {
  font-size: 18px;
  color: #00A65A;
  cursor: pointer;
}
.down-control .comparison {
  width: 191px;
}
.down-control .property-name,
.down-control .value {
  width: 200px !important;
}
.graph-tooltip {
  position: absolute;
  color: white;
  display: none;
  border: 1px solid #009049;
  padding: 2px;
  background-color: #00A65A;
  opacity: 0.80;
  z-index: 1000;
}
.graph-checkboxes input {
  margin-left: 10px;
  margint-right: 2px;
}
#data-holder {
  color: #00A65A;
}
.sidebar-menu .project-name {
  color: white !important;
  font-size: 15px !important;
}
.tab-div {
  display: none;
}
form.no-style-form {
  margin-bottom: 0px;
}
.big-error-message {
  font-size: 30px;
  font-weight: bold;
}
