.alerting table td,
.alerting table th {
  padding: 2px;
}
.alerting table.current-alerting {
  margin-bottom: 50px;
}
.alerting table.current-alerting td:nth-of-type(1) {
  width: 100px;
}
.alerting table.current-alerting td:nth-of-type(2) {
  padding-right: 30px;
}
.alerting input,
.alerting select {
  height: 32px;
}
